<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        sm12
        md9
        lg8
      >
        <v-row cols="12">
          <v-col md="6">
            <v-img
              v-if="showAmount"
              height="80vh"
              class="white--text align"
              :src="require(`../assets/img/home/${$route.params.img}.jpeg`)"
            >
            </v-img>
          </v-col>
          <v-col
            md="6"
            v-if="showAmount"
          >
            <v-card
              class="mx-auto"
              tile
            >
              <v-toolbar
                color="primary"
                dark
              >
                <v-icon class="mr-2">mdi-barn</v-icon>
                <v-toolbar-title>สินค้าประเภทผัก</v-toolbar-title>
              </v-toolbar>

              <v-list>
                <v-list-item-group
                  v-model="selectedItem"
                  color="primary"
                >
                  <v-list-item
                    v-for="(item, i) in items"
                    :key="i"
                    @click="itemClick('Test')"
                  >
                    <v-list-item-content>
                      <v-list-item-title v-text="item.text"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-card>
          </v-col>

          <v-row
            md="12"
            v-if="showDetails"
          >
            <v-col>
              <v-card
                class="mx-auto"
                tile
              >
                <v-toolbar
                  color="primary"
                  dark
                >
                  <v-icon
                    class="mr-2"
                    @click="back"
                  >mdi-arrow-left-thick</v-icon>
                  <v-toolbar-title>รายชื่อศูนย์ [ฟักทองญี่ปุ่น]</v-toolbar-title>
                </v-toolbar>

                <v-simple-table>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-left">
                          พื้นที่โครงการ
                        </th>
                        <th class="text-left">
                          อำเภอ
                        </th>
                        <th class="text-left">
                          จังหวัด
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="item in detailsItems"
                        :key="item.name"
                      >
                        <td>{{ item.field }}</td>
                        <td>{{ item.amp }}</td>
                        <td>{{ item.prov }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card>
            </v-col>

            <v-col>
              <l-map
                style="z-index: 0; height: 80vh; width: 60vh"
                :zoom="zoom"
                :center="center"
                @update:zoom="zoomUpdated"
                @update:center="centerUpdated"
                @update:bounds="boundsUpdated"
              >
                <l-tile-layer :url="url"></l-tile-layer>
                <l-marker
                  v-for="marker in markers"
                  :key="marker.id"
                  :visible="marker.visible"
                  :lat-lng.sync="marker.position"
                  :icon="marker.icon"
                >
                  <l-popup :content="marker.tooltip" />
                  <l-tooltip :content="marker.tooltip" />
                </l-marker>
              </l-map>
            </v-col>
          </v-row>
        </v-row>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import L from "leaflet";
import { LMap, LTileLayer, LMarker, LTooltip, LPopup } from "vue2-leaflet";

export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip,
    LPopup,
  },
  data() {
    return {
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      zoom: 10,
      center: [18.774752, 100.772736],
      bounds: null,
      showAmount: true,
      showDetails: false,
      selectedItem: 1,
      markers: [
        {
          id: "m1",
          position: { lat: 18.8928458528, lng: 101.0224905285 },
          tooltip: "โป่งคำ",
          visible: true,
        },
        {
          id: "m2",
          position: { lat: 18.8644272556, lng: 100.6771460707 },
          tooltip: "สะเนียน",
          visible: true,
        },
        {
          id: "m3",
          position: { lat: 51.005, lng: -0.09 },
          tooltip: "tooltip for marker3",
          draggable: true,
          visible: true,
        },
        {
          id: "m4",
          position: { lat: 50.7605, lng: -0.09 },
          tooltip: "tooltip for marker4",
          draggable: true,
          visible: false,
        },
      ],
      pane: "Test",
      items: [
        { text: "ฟักทองญี่ปุ่น", icon: "mdi-clock", amount: "1,232" },
        { text: "ผักกาดขาวเบา", icon: "mdi-account", amount: "3,412" },
        { text: "ผักกาดเขียว", icon: "mdi-flag", amount: "2,332" },
        { text: "พริกเขียว", icon: "mdi-flag", amount: "3,312" },
        { text: "พริกจินดา", icon: "mdi-flag", amount: "4,321" },
        { text: "พริกแดง", icon: "mdi-flag", amount: "1,234" },
        { text: "พริกสามสี", icon: "mdi-flag", amount: "659" },
      ],
      detailsItems: [
        {
          field: "สถาบันวิจัยและพัฒนาพื้นที่สูง ส่วนกลาง-น่าน",
          amp: "ภูเมือง",
          prov: "น่าน",
          amount: "99",
        },
        {
          field: "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวง โป่งคำ",
          amp: "สันติสุข",
          prov: "น่าน",
          amount: "99",
        },
        {
          field: "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวง สะเนียน",
          amp: "เมืองน่าน",
          prov: "น่าน",
          amount: "99",
        },
        {
          field: "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวง ถ้ำเวียงแก",
          amp: "ผาทอง",
          prov: "น่าน",
          amount: "99",
        },
        {
          field: "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวง ปางยาง",
          amp: "ปัว",
          prov: "น่าน",
          amount: "99",
        },
        {
          field: "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวง วังไผ่",
          amp: "สองแคว",
          prov: "น่าน",
          amount: "99",
        },
        {
          field:
            "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวงเพื่อแก้ปัญหาพื้นที่เฉพาะ น้ำแป่ง",
          amp: "ท่าวังผา",
          prov: "น่าน",
          amount: "99",
        },
        {
          field:
            "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวงเพื่อแก้ปัญหาพื้นที่เฉพาะ แม่จริม",
          amp: "แม่จริม",
          prov: "น่าน",
          amount: "99",
        },

        {
          field:
            "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวงเพื่อแก้ปัญหาพื้นที่เฉพาะ น้ำเคิม",
          amp: "นาหมื่น",
          prov: "น่าน",
          amount: "99",
        },

        {
          field:
            "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวงเพื่อแก้ปัญหาพื้นที่เฉพาะ น้ำแขว่ง",
          amp: "นาหมื่น",
          prov: "น่าน",
          amount: "99",
        },

        {
          field:
            "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวงเพื่อแก้ปัญหาพื้นที่เฉพาะ ขุนสถาน",
          amp: "นาน้อย",
          prov: "น่าน",
          amount: "99",
        },
        {
          field:
            "โครงการพัฒนาพื้นที่สูงแบบโครงการหลวงเพื่อแก้ปัญหาพื้นที่เฉพาะ บ่อเกลือ",
          amp: "บ่อเกลือ",
          prov: "น่าน",
          amount: "99",
        },
        {
          field:
            "พื้นที่โครงการรักษ์น้ำ ลุ่มน้ำขุนน่าน",
          amp: "เฉลิมพระเกียรติ",
          prov: "น่าน",
          amount: "99",
        },
      ],
    };
  },
  methods: {
    itemClick() {
      this.showAmount = false;
      this.showDetails = true;
    },
    back() {
      this.showAmount = true;
      this.showDetails = false;
    },
    zoomUpdated(zoom) {
      this.zoom = zoom;
    },
    centerUpdated(center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
  },
  mounted() {},
};
</script>



